import Vue from 'vue'
import Router from 'vue-router'
import lczBlogRouters from '@/views/blog/lcz-blog-router.js'
import lczOsChinaRouters from '@/views/oschina/lcz-oschina-router.js'

Vue.use(Router)

const routes = []
routes.push(...lczBlogRouters)
routes.push(...lczOsChinaRouters)

export default new Router({
  mode: 'hash',
  routes: routes
})



