const lczBlogRouters = [
  {
    path: '/lczBlogMain',
    name: 'lczBlogMain',
    component: () => import('@/views/blog/lcz-blog-main'),
    children: [
      {
        path: '/lczBlogMain/index',
        component: () => import('@/views/blog/index/lcz-index-main')
      },
      {
        path: '/lczBlogMain/article/:type/:id',
        component: () => import('@/views/blog/article/lcz-article-content')
      },
      {
        path: '/lczBlogMain/code',
        component: () => import('@/views/blog/code/lcz-code-main')
      },
      {
        path: '/lczBlogMain/article',
        component: () => import('@/views/blog/article/lcz-article-main')
      },
      {
        path: '/lczBlogMain/notes',
        component: () => import('@/views/blog/notes/lcz-notes-main')
      },
      {
        path: '/lczBlogMain/message',
        component: () => import('@/views/blog/message/lcz-message-main')
      },
      {
        path: '/lczBlogMain/upper',
        component: () => import('@/views/blog/upper/lcz-upper-main')
      }
    ]
  }
]

export default lczBlogRouters



